@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@700&display=swap);
*{font-family:'Open Sans', sans-serif}.title{text-align:center}

.userInfo{width:500px;display:flex;justify-content:space-between;align-items:center}.userInfo__item{font-size:20px;font-weight:bold}

.todo{display:flex;justify-content:space-between;align-items:center;grid-gap:12px;gap:12px;padding:12px 16px;border:4px solid transparent;border-radius:10px;background-color:#6D8299}.todo-status{border-color:#F4D19B}.todo-status h3{color:#F4D19B}.todo-status--completed{border-color:#9CC094;background-color:#F6F6F6}.todo-status--completed h3{color:#9CC094}.todo__task{font-size:14px}

.todoList{display:flex;flex-direction:column;max-width:1200px;margin:auto;grid-gap:20px;gap:20px}

